@import url("https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100;8..144,400;8..144,700&family=Roboto+Mono:wght@100;400;700&family=Roboto+Slab:wght@100;400;700&family=Roboto:wght@100;400;700&display=swap");
*:where(:not(html,
iframe,
canvas,
img,
svg,
video,
audio):not(svg *,
symbol *)) {
  all: unset;
  display: revert;
}
*,
:before,
:after {
  box-sizing: border-box;
}
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}
a,
button {
  cursor: revert;
}
ol,
ul,
menu,
summary {
  list-style: none;
}
img {
  max-inline-size: 100%;
  max-block-size: 100%;
}
table {
  border-collapse: collapse;
}
input,
textarea {
  -webkit-user-select: auto;
}
textarea {
  white-space: revert;
}
meter {
  -webkit-appearance: revert;
  appearance: revert;
}
:where(pre) {
  all: revert;
  box-sizing: border-box;
}
::placeholder {
  color: unset;
}
:where([hidden]) {
  display: none;
}
:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}
:where([draggable="true"]) {
  -webkit-user-drag: element;
}
:where(dialog:modal) {
  all: revert;
  box-sizing: border-box;
}
::-webkit-details-marker {
  display: none;
}
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  *,
  :before,
  :after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
    transition-duration: 0.01ms !important;
  }
}
html,
body {
  height: 100%;
  overflow: inherit;
}
* html,
* body {
  min-height: 100%;
}
* html #off-canvas {
  height: 100%;
}
#off-canvas {
  display: grid;
  min-height: 100%;
  grid-template-rows: auto 1fr auto;
}
form,
label,
input,
input[type=text],
input[type=email],
input[type=url],
input[type=password],
select,
textarea {
  min-width: 0;
  max-width: 100%;
  resize: vertical;
}
html,
body,
* {
  font-family: 'Roboto', sans-serif, ui-sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto Slab', serif, ui-serif;
  text-wrap: balance;
}
code,
pre {
  font-family: 'Roboto Mono', monospace, ui-monospace;
}
h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.5em;
}
h3 {
  font-size: 1.17em;
}
h4 {
  font-size: 1em;
}
h5 {
  font-size: 0.83em;
}
h6 {
  font-size: 0.67em;
}
body.toolbar-horizontal #off-canvas {
  min-height: calc(100dvh - 60px);
}
body.toolbar-vertical #off-canvas {
  min-height: calc(100dvh - 49px);
}
body.maintenance-page #off-canvas {
  place-items: center;
  grid-template-rows: auto;
}
.wrapper {
  --width__default: 2033px;
  width: min(var(--width__default), 100% - 2.25rem);
  margin-inline: auto;
}
a.feed-icon {
  overflow: hidden;
  display: block;
  max-width: 1.3rem;
  max-height: 1.3rem;
  margin-top: 0.75rem;
}
.site-header__nav-wrapper .menu-button {
  position: absolute;
  visibility: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.page-title {
  margin-bottom: 0.5rem;
}
.form-item {
  display: flex;
  flex-direction: column;
}
.site-branding__logo-image {
  max-width: 5rem;
}
.site-branding__name * {
  font-family: 'Roboto Slab', serif, ui-serif;
}
textarea {
  width: 100%;
}
.copyright-footer {
  margin-bottom: 1.25rem;
}
.block-system-branding-block {
  display: flex;
  align-items: center;
  max-height: 5rem;
}
section.site-content {
  line-height: 1.25;
}
footer {
  padding-block: 1.25rem;
  min-height: 4.2rem;
}
footer .region--footer {
  display: grid;
  grid-gap: 0.5rem;
}
footer .region--footer nav .menu {
  list-style: square;
  padding-left: 1rem;
}
header .site-header__inner {
  display: flex;
  column-gap: 1rem;
  flex-wrap: wrap;
}
header .site-header__inner .site-header__brand-wrapper,
header .site-header__inner .site-header__nav-wrapper {
  display: grid;
}
.with-title .page-title-block {
  margin-bottom: 0.5rem;
}
figure.hero {
  padding-inline: 0;
}
main {
  max-width: 100dvw;
}
main > figure,
main > section {
  max-width: 100%;
}
.local-tasks-block {
  margin-block: 0.5rem 0.75rem;
}
nav.local-tasks__primary,
nav.local-tasks__secondary {
  display: grid;
}
nav.local-tasks__primary ul {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1.25rem;
  max-width: max-content;
}
nav.local-tasks__primary ul li a {
  display: block;
  padding-block: 0.5rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(6rem, 1fr));
  grid-auto-flow: column;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li {
  min-width: 6rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li a {
  display: grid;
  place-content: center;
  padding-block: 2rem;
}
.region--branding {
  display: grid;
}
.region--navigation {
  display: grid;
  justify-items: end;
}
body.maintenance-page header img {
  max-height: 55dvh;
}
